import {SERVER_LINK} from '../components/Global';
import Axios from 'axios';

const API = SERVER_LINK + "/api/v1/";

function getConfessFrom(range, id, callback) {
    Axios.get(API + 'get_confess/' + range + '/' + id).then((res) => {
        return callback(res.data);
    });
}

function sendEmotionToConfess(emotion, id, callback) {
    //GET USER IP
    Axios.get("https://www.cloudflare.com/cdn-cgi/trace").then((result) => {
        let client_ip = result.data.split('\n')[2].split("=")[1];

        //SEND EMOTION TO API
        Axios.post(API + 'client_send_emotion/' + id, {emotion: emotion, user_id: "admin", date: new Date(), client_ip: client_ip}).then((res) => {
            return callback(res.data);
        });
    })
}

export {
    getConfessFrom,
    sendEmotionToConfess
}

