import React, {Component} from 'react';
import Footer from '../components/Footer';
import DocumentMeta from 'react-document-meta';

class ConfessSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: "Hi!",
      active: false,
      text_location: 0,
      background_class: "section white"
    }
  }

  handleChangeText = () => {
    let text = ["","Đây là trang web tui làm ra chỉ để cho mình Nhi nhìn thấy","Nơi để tui nói những thứ mà bình thường tui không thể nói", "Bắt đầu nha...", "Có thể Nhi không nhớ nhưng ngay từ lần đầu gặp tại trường Bình Phú", "Không hiểu sao tui có ấn tượng khi lần đầu nhìn thấy Nhi", "và cho đến bây giờ, tui vẫn không quên hình ảnh của Nhi ngày hôm đó", "Sau khi nhắn tin tui cũng tìm thấy những điểm có thể chung giữa tui và Nhi là...", "Yêu âm nhạc...", "Có thể Nhi cũng thích màu xanh...", "màu hồng ?", "và còn những điều khác tui sẽ tìm hiểu dần...", "Và chắc Nhi cũng đang thắc mắc là vì sao tui thích Nhi đúng hong ?", "Tui thích chính sự đơn giản", "nhẹ nhàng...", "và cả một chút sự dễ thương của Nhi", "nhưng chung quy lại...", "tui thích mọi thứ từ Nhi...vì", "đã thích thì cần gì lý do", "So...", "Mình quen nhau nha"]
    console.log(this.state.text_location + 1);
    if(!this.state.active) {
      let background = ["section blue", "section pink", "section white", "section end"];
      if(this.state.text_location + 1 === 9) {
        this.setState({
          background_class: background[0]
        })
      }else if(this.state.text_location + 1 === 10) {
        this.setState({
          background_class: background[1]
        })
      }else if(this.state.text_location + 1 === 20){
        this.setState({
          background_class: background[3]
        })
      }else {
        this.setState({
          background_class: background[2]
        })
      }

      this.setState({
        text:text[this.state.text_location + 1],
        active: true,
        text_location: this.state.text_location + 1
      })
  
      setTimeout(() => {
        this.setState({
          active: false
        })
      }, 1000)
    }
  }

  render() {
    return (
      <div className = {this.state.background_class}>
        <div className = "row container" style={{height: "100vh", display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
          <a onClick={this.handleChangeText} style={{color: "#414141"}}><h1 className={this.state.active ? "animate_title" : ""} style={this.state.text_location === 20 ? {color: "#e94256"} : this.state.background_class !== "section white" ? {color: "#fff"} :  null}>{this.state.text}</h1></a>
          <a href="https://www.facebook.com/nhatsdevil.mc" style={{color: "#414141"}}><span  style={this.state.text_location === 20 ? {color: "#e94256"} : this.state.background_class !== "section white" ? {color: "#fff"} :  null}>Welcome Ngọc Nhi !</span></a>
        </div>
      </div>
    )
  }
}

class Home extends Component {
    render() {
      const meta = {
          title: "Laven Confession",
          description: "Laven Confession. Là nơi cho bạn có thể tâm sự, gửi những ý kiến của bạn và hoàn toàn được ẩn danh. Vậy tại sao chúng ta không đăng một cofess để miêu tả tâm trạng hiện giờ của mình? Tại sao không?",
          canonical: window.location.href,
          image: "%PUBLIC_URL%/resources/images/thumbnail/thumbnail.png",
          meta: {
            charset: 'utf-8',
            name: {
              image: '%PUBLIC_URL%/resources/images/thumbnail/thumbnail.png'
            }
          }
        };

      return (
        <DocumentMeta {...meta}>
          <div>
            <title>Laven Confession</title>
            <div className = {localStorage.getItem('dark_mode') == 'true' ? "section dark" : "section white"}>
              <div className = "row container" style={{height: "100vh", display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
                <a href="https://www.facebook.com/nhatsdevil.mc" style={localStorage.getItem('dark_mode') == 'true' ? {color: "#fff"} : {color: "#414141"}}><h1>Hi</h1></a>
                <a href="https://www.facebook.com/nhatsdevil.mc" style={localStorage.getItem('dark_mode') == 'true' ? {color: "#ffffff96"} : {color: "#414141"}}><span>Designed by Laven Studio in Ho Chi Minh city</span></a>
                <a href="https://www.facebook.com/nhatsdevil.mc" style={localStorage.getItem('dark_mode') == 'true' ? {color: "#ffffff96", fontSize: "12px", fontWeight: "500", position: "absolute", bottom: "8px", right: "16px"} : {color: "#414141", fontSize: "12px", fontWeight: "500", position: "absolute", bottom: "8px", right: "16px"}}><span>Build [13.DEC.2020]</span></a>
              </div>
            </div>
          </div>
        </DocumentMeta>
      )
    }
}

export default Home;